import React, { Component } from "react";

import "./styles.css";
import axios from "axios";

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }
  componentDidMount() {
    axios.get("https://jsonplaceholder.typicode.com/posts").then((e) => {
      this.setState({
        data: e.data
      });
    });
  }
  render() {
    return (
      <div>
        <ul>
          {this.state.data.map((e) => (
            <li>{e.title}</li>
          ))}
        </ul>
      </div>
    );
  }
}

export default App;
